import React from "react";

import GithubMark from "./GitHub-Mark-64px.png";

type Props = React.AllHTMLAttributes<HTMLAnchorElement>;

const GithubLink = (props: Props) => (
  <a
    href="https://github.com/hasparus/react-rough-logo"
    {...props}
  >
    <img src={GithubMark} alt="link to github" />
  </a>
);

export default GithubLink;
